import OPTIONS from "../datas/options";

const sortByKey = key => {
  const compare = (a, b) => {
    return a[key].localeCompare(b[key]);
  };

  return compare;
}

const DATAS = OPTIONS.sort(sortByKey("title"));

const byType = type => {
  return DATAS.filter(data => data["type"] === type);
}

const uniqueTitles = (options = {}) => {
  let datas = DATAS;
  if (options["type"]) {
    datas = byType(options["type"]);
  }

  const titles = {};
  datas.forEach(data => {
    titles[data["title"]] = 1;
  });
  return Object.keys(titles);
}

const uniqueBeers = () => {
  let beers = {};
  OPTIONS.forEach(data => {
    if (data["beer"]) {
      beers[data["beer"]] = 1;
    }
  });

  return Object.keys(beers);
};

const byTitle = title => {
  return DATAS.filter(data => data["title"] === title);
}


export { byTitle, byType, sortByKey, uniqueBeers, uniqueTitles };
